import 'date-fns';
import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Grid,Paper, Typography, Button , TextField, Select, FormLabel, FormHelperText, Chip } from "@material-ui/core";
import { useHistory } from "react-router-dom";
import MUIRichTextEditor from 'mui-rte'
import { createMuiTheme, Theme, MuiThemeProvider } from '@material-ui/core/styles'
import { Api } from '../common/api';
import { EditorState, ContentState, convertFromHTML, convertToRaw } from 'draft-js'
import Spinner from "../common/Spinner";
import {stateToHTML} from 'draft-js-export-html';
import defaultFoodImage from "../../assets/food-default.png";
import DateFnsUtils from '@date-io/date-fns';
import Alert from '@material-ui/lab/Alert';
import Autocomplete from '@material-ui/lab/Autocomplete';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';


const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
  textField: {
    fontSize:50
  },
  label:{
    fontSize:50
  }
});






let AddSymptom = (props: any) => {

    const classes = useStyles(); 
    const [emotion, setEmotion] = useState("")
    const [emotions, setEmotions] = useState([])
    const [loading, setLoading] = useState(true)
    const [meals, setMeals] = useState([])
    const [meal, setMeal] = useState("")
    const [isupdate, setIsupdate] = useState(false)
    const history = useHistory();
    const [buchid, setBuchid] = useState(0)
    const [uhrzeit, setuhrzeit] = useState(0)
    const [reloadGroups, setReloadGroups] = useState(0)
    const [errormessagelist, setErrormessagelist] = useState<string[]>([])
    const [selectedDate, setSelectedDate] = React.useState(new Date(Date.now()));
    const [symptoms, setSymptoms] = useState([])
    const [rsymptoms, setRsymptoms] = useState<{id:number,name:string}[]>([])


    useEffect(() => {
        if(reloadGroups === 0){
            const fetchData = async() => {
                setLoading(true)
                //Getting lebensmittels list
                let method = "get";
                let symptomUrl = "receipe/getSymptoms/"


                let respArray = []

                respArray = await Promise.all([
                Api(`prod`).get(symptomUrl,{}),
                ])

                let symptoms = respArray[0] 
                setSymptoms(symptoms.data)
                setuhrzeit(selectedDate.getTime())

                if(props.match.params.id){
                  let buchUrl = "diary/getBuchById/"+props.match.params.id
                  let buch = await Api(`prod`).get(buchUrl,{})
                  setuhrzeit(buch.data.uhrzeit)
                  setBuchid(buch.data.id);
                  setIsupdate(true)
                  
                  setSelectedDate(new Date(new Date(buch.data.uhrzeit).toUTCString()))
                  setRsymptoms(buch.data.buchDictSymptomList);
                }
                
                
                setLoading(false)
                
            }
    
           
            fetchData();
            
    
        }
        
    
      }, [reloadGroups]);


  


    function createFormData(object: any, form?: FormData, namespace?: string): FormData {
        const formData = form || new FormData();
        for (let property in object) {
            if (!object.hasOwnProperty(property) || !object[property]) {
                continue;
            }
            const formKey = namespace ? `${namespace}|${property}` : property;
            if (object[property] instanceof Date) {
                formData.append(formKey, object[property]);
            } else if (typeof object[property] === 'object' && !(object[property] instanceof File)) {
                createFormData(object[property], formData, formKey);
            } else {
                formData.append(formKey, object[property]);
            }
        }
        return formData;
    }


    const handleSubmit = (e: any) =>{ 
        // Prevent default behavior
        e.preventDefault();

        let rsymptomsArray = [];
        for(let i in rsymptoms){
            rsymptomsArray.push(rsymptoms[i].id.toString())
        }


        let postData = {
            "uhrzeit": new Date(uhrzeit).toISOString(),
            "symptoms": rsymptomsArray,
            "type":"symptom"
        }

        
        const data = createFormData(postData);
        // Access FormData fields with `data.get(fieldName)`
        // For example, converting to upper case

        if(isupdate){
          
            let url = "diary/updateDiary/"+buchid
            const updateReceipe = async() => {
              try{
                const res =  await Api(`prod`).post(url,
                    {
                        "data":data,
                        "headers": {
                            'content-type': 'multipart/form-data'
                        }
                    },{});
                history.push("/dashboard");
              }catch(error){
                if(error.response.data.messageList != undefined && error.response.data.messageList.length > 0){
                  setErrormessagelist(error.response.data.messageList);
                  window.scrollTo(0, 0)
                  setReloadGroups(prev => ++prev);
                }
              } 
             
          
          }
          updateReceipe();
            
           
           
        }else{

          
            let method = "post";
            let url = "diary/createDiary/"

            
            const saveReceipe = async() => {
              try{
                const res =  await Api(`prod`).post(url,
                    {
                        "data":data,
                        "headers": {
                            'content-type': 'multipart/form-data'
                        }
                    },{});
                history.push("/dashboard");
            }catch(error){
              if(error.response.data.messageList != undefined && error.response.data.messageList.length > 0){
                setErrormessagelist(error.response.data.messageList);
                window.scrollTo(0, 0)
                setReloadGroups(prev => ++prev);
              }
            } 
          }
            saveReceipe();
          
           
            
        }
    }




 

  const handleDateChange = ((date: any) => {
    setSelectedDate(date);
    setuhrzeit(date.getTime())
  });

  return (
    
    <div>
    {loading ? (
        <Spinner isLoading={loading}  />
    ) : (
        <Grid container justify="center" spacing={4}  direction="row">
            <Grid item  xs={11} sm={11} md={10}>
                <Typography component="h1" variant="h4"> Add Symptom </Typography>
                {errormessagelist.map((message: string) => (<Alert severity="error">{message}</Alert>))}
            </Grid>
            <Grid item  xs={11} sm={11} md={10}>
                <Grid container direction="column" justify="center"  className="food-diary-page">
                    <Paper variant="elevation" elevation={2} className="food-diary-background">
                        <Grid item>
                            <form method="post"  onSubmit={(e) => handleSubmit(e)}>
                                <Grid container direction="column" spacing={2} >
                                    <Grid item  xs={12} sm={12} md={12} >
                                        <FormLabel component="legend" required={true}>Select Symptoms</FormLabel>
                                        <Autocomplete
                                          multiple
                                          id="tags-standard"
                                          options={symptoms}
                                          getOptionLabel={(option: {id:number,name:string}) => option.name}
                                          value={rsymptoms}
                                          onChange={(e : any, value) => {
                                              setRsymptoms(value)
                                            }
                                          }
                                          renderInput={(params) => (
                                            <TextField
                                              {...params}
                                              variant="standard"
                                              placeholder="Select Symptoms"
                                            />
                                          )}
                                        />
                                   
                                    </Grid>
                                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                        <Grid container direction="row" style={{padding:"10px"}} >
                                            <Grid item  xs={12} sm={12} md={3} style={{paddingTop:"20px" }}>
                                                <KeyboardDatePicker
                                                  margin="normal"
                                                  id="diary-date"
                                                  label="Select Date"
                                                  format="MM/dd/yyyy"
                                                  value={selectedDate}
                                                  onChange={handleDateChange}
                                                  KeyboardButtonProps={{
                                                    'aria-label': 'change date',
                                                  }}
                                                />
                                            </Grid>
                                            <Grid item  xs={12} sm={12} md={3} style={{paddingTop:"20px" }}>
                                                <KeyboardTimePicker
                                                  margin="normal"
                                                  id="diary-time"
                                                  label="Select Time"
                                                  value={selectedDate}
                                                  onChange={handleDateChange}
                                                  KeyboardButtonProps={{
                                                    'aria-label': 'change time',
                                                  }}
                                                />
                                            </Grid>
                                    </Grid>
                                    </MuiPickersUtilsProvider>



                                 

                                  
                                        
                                
                                    <Grid item xs={12} sm={12} md={12} >
                                   
                                        <Grid container style={{ paddingTop: 100 }}   justify="flex-end">
                                        
                                            <Grid item  xs={12} sm={12} md={3} style={{textAlign:"right"}}>
                                                <Button style={{marginRight:5}} fullWidth={false} onClick = {() => {history.push('/dashboard')}} variant="contained"  color="primary"  className="button-block" > Back to Dashboard </Button>
                                                <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" > Save Symptom </Button>
                                            </Grid>
                                          
                                        </Grid>
                                     

                                        
                                    </Grid>
                                </Grid>
                            </form>
                        </Grid>
                    </Paper>
                </Grid>
            </Grid>
        </Grid>

    )}
    </div>
      
    
   
     
  );
    
  
}


export default AddSymptom;