class PeopleList extends React.Component {

  state = {
    selectedIndex:this.props.selectedUser,
    users:[]
  }

  componentDidMount() {
    let userUrl = "user/"
    if(localStorage.getItem('userRole') == "client"){
        userUrl =  "user/?role=coach";
    }else if(localStorage.getItem('userRole') == "coach"){
        userUrl =  "user/?role=client";
    }else{
        userUrl =  "user/?role=admin";
    }


    let fetchuser = async() => {
        let users = await Api(`prod`).get(userUrl,{})
        this.setState({
            users : users.data.result
        });

    }
    
    fetchuser();
    this.setState({
        checkoutAlert: false
    });
    
  }


  setSelectedIndex = (index, item) => {
    this.setState({
      selectedIndex:item.id
    });

    this.props.handleSelectedUser(item.id);
  }

  getListItem = (index,item) => {
    if(this.state.selectedIndex == item.id){
      return (
        <ListItem onClick={() => {this.setSelectedIndex(index, item)}} selected={true} dense button style={{padding: "20px"}}>
          <picture className="user-picture">
            {(item.profilepic != "" && item.profilepic != null)?(
                <Avatar src={`${localStorage.getItem('base_backend_server_domain')}/media/${item.profilepic}`} alt={`${item.email}`} />
            ):(
                <Avatar src={defaultProfilePic} alt={`${item.email}`} />
            )
            }
    
          </picture>
          <ListItemText style={{paddingLeft: "10px"}} primary={item.vname+" "+item.nname} />
        </ListItem>
      );
    }else{
      return (
        <ListItem  onClick={() => {this.setSelectedIndex(index, item)}} selected={false} dense button style={{padding: "20px"}}>
          <picture className="user-picture">
            {(item.profilepic != "" && item.profilepic != null)?(
                <Avatar src={`${localStorage.getItem('base_backend_server_domain')}/media/${item.profilepic}`} alt={`${item.email}`} />
            ):(
                <Avatar src={defaultProfilePic} alt={`${item.email}`} />
            )
            }
    
          </picture>
          <ListItemText style={{paddingLeft: "10px"}} primary={item.vname+" "+item.nname} />
        </ListItem>
      );
    }
  }

  render() {

    const { classes } = this.props;

    let images = [
      { name: 'James Roberts', avatar: 'https://randomuser.me/api/portraits/men/61.jpg' },
      { name: 'Andy Brown', avatar: 'https://randomuser.me/api/portraits/men/62.jpg' },
      { name: 'Peter Smith', avatar: 'https://randomuser.me/api/portraits/men/63.jpg' },
      { name: 'Jennifer Todd', avatar: 'https://randomuser.me/api/portraits/women/60.jpg' } 
    ];


    return (
      <Grid container justify="center" spacing={4}  direction="row">
        
        <Grid item>
          <List className={classes.root}>
              {this.state.users.map((user, index) => (
                <React.Fragment key={index}>
                    {(user.id != this.props.loginSuccessResponse.data.user_id)?(this.getListItem(index,user)):("")}
                    {index !== images.length-1 && <Divider  />}
                </React.Fragment>
              ))}
          </List>
        </Grid>
      </Grid>
    );
  }

}

