class ChatText extends React.Component {

 

  state = {
    modalstate: false,
    currentchattext: "",
    chatTexts: this.props.chatTexts,
    currentUserId:0,
    profilePic:""
  };


  componentDidMount() {
    let profilePic = this.props.loginSuccessResponse.data.profilepic;
    
    this.setState({
        profilePic: `${localStorage.getItem('base_backend_server_domain')}/media/${profilePic}`,
        currentUserId: this.props.loginSuccessResponse.data.user_id
    });  
    
  }

  componentDidUpdate(prevProps, prevState) {
    this.scrollToBottom()
  }

  createFormData = (object, form, namespace)  =>{
    const formData = form || new FormData();
    for (let property in object) {
        if (!object.hasOwnProperty(property) || !object[property]) {
            continue;
        }
        const formKey = namespace ? `${namespace}|${property}` : property;
        if (object[property] instanceof Date) {
            formData.append(formKey, object[property].toISOString());
        } else if (typeof object[property] === 'object' && !(object[property] instanceof File)) {
            this.createFormData(object[property], formData, formKey);
        } else {
            formData.append(formKey, object[property]);
        }
    }
    return formData;
  }

  

  handleChatClick = () => {
    if(this.state.currentchattext.trim() !== ""){
      //selectedUser
      let postData = {
        "chat_message": this.state.currentchattext,
        "person_id_one": this.props.selectedUser,
        "person_id_two": this.state.currentUserId
      }
      let data = this.createFormData(postData);
      let method = "post";
      let url = "user/appendUserChat/"

      const sendChatMessage = async() => {
          const res =  await Api(`prod`).post(url,
              {
                  "data":data,
                  "headers": {
                      'content-type': 'multipart/form-data'
                  }
              },{});
      } 
      sendChatMessage();


      let newMsgList = this.props.chatTexts;
      newMsgList.push({ name: 'Andy Brown', user_id: this.state.currentUserId, message: this.state.currentchattext })
      console.log(newMsgList)

      this.props.setChatText(newMsgList);


      this.setState({
        currentchattext: ""
      })
    }
    
  }

 

  
  onEmojiClick = (event, emojiObject) => {

    let newChatText = this.state.currentchattext+" "+emojiObject.emoji;

 

    this.setState({
      currentchattext: newChatText
    })
      
  };

  handleOpen = () => {
    this.setState({
      modalstate:true
    })
  };

  handleClose = () => {
    this.setState({
      modalstate:false
    })
  };

  modalBody = (
    <div >
      <p id="simple-modal-description">
        <Picker onEmojiClick={this.onEmojiClick} />
      </p>
    </div>
  );

  scrollToBottom = () => {
    this.messagesEnd.scrollIntoView({ behavior: "smooth" });
  }

  render() {
    const { classes } = this.props;





    return (
      <section className={classes.root}>
        <Paper className={classes.chatInputPaper} elevation={4}>
          <div className={classes.chatInput} style={{maxHeight: "436px"}}>

            {this.props.chatTexts.map((item, index) => {
              return (
                <React.Fragment key={index}>
                  <div className={classes.chatRoomMessage}>
                    <Avatar alt='Remy Sharp' src={`${localStorage.getItem('base_backend_server_domain')}/media/${this.props.userdata[parseInt(item.user_id)].profilepic}`} />
                    <div className={classes.messageText}>
                      {item.message}
                    </div>
                  </div>
                  <Divider className={classes.divider}  />
                </React.Fragment>
              );
            })}
            <div style={{ float:"left", clear: "both" }}
             ref={(el) => { this.messagesEnd = el; }}>
            </div>

          </div>
          
        </Paper>
        <Paper className={classes.composeInputPaper} elevation={4}>
          <Input 
            onChange={(e) => this.setState({
              currenttext: this.setState({currentchattext: e.target.value})
            })} 
            value={this.state.currentchattext}
            classes={{ root: classes.composeInput }} 
            multiline={true} 
            fullWidth={true} 
            disableUnderline={true} 
            placeholder={"Send a message!"} 
            startAdornment={
              <IconButton  onClick={this.handleOpen}><EmojiEmotionsIcon/></IconButton>
            }
            endAdornment={<div><IconButton onClick={this.handleChatClick}><SendIcon className={classes.searchIcon}  /></IconButton></div>}
          />
        </Paper>
        <Modal
          style={{marginTop:"10%",marginLeft:"50%",width:"10%"}}
          open={this.state.modalstate}
          onClose={this.handleClose}
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
        >
          {this.modalBody}
        </Modal>
      </section>
    );
  }

}

