import React from 'react'

import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import interactionPlugin from "@fullcalendar/interaction"; 
import '@fullcalendar/core/main.css';
import "@fullcalendar/daygrid/main.css";
import "@fullcalendar/timegrid/main.css";
import { Grid, Typography, Paper, Button  } from "@material-ui/core";


export default class Schedule extends React.Component {
  state = {
    canDrop: true
  };

  setCanDrop = () => {};
  calendarComponentRef = React.createRef();

  state = {
    calendarWeekends: true,
    calendarEvents: [
      // initial event data
      { title: "Event Now", start: new Date() }
    ]
  };




  render() {
    return (
      <Grid container justify="center" spacing={4}  direction="row">
      
        <Grid item  xs={10} sm={10} md={10}>
          <Button fullWidth={false} onClick = {() => {this.props.history.push('/dashboard')}} variant="contained" style={{marginBottom: "30px"}} color="primary" type="submit" className="button-block" > Back to Dashboard </Button>
        </Grid>
        <Grid item  xs={10} sm={10} md={10}>
          <Grid container justify="center" spacing={4}  direction="row">
            <Paper variant="elevation" elevation={2} className="food-diary-background">
              <Grid item  xs={12} sm={12} md={12}>
                  <Typography component="h1" variant="h4"> Schedule Meeting </Typography>
              </Grid>
              <Grid item  xs={12} sm={12} md={12}>
                <div className="demo-app">
                  
                  <div className="demo-app-calendar">
                    <FullCalendar
                      timezone= 'UTC'
                      editable={true}
                      droppable={false}
                      defaultView="timeGridWeek"
                      headerToolbar={{
                        left: "prev,next today",
                        center: "title",
                        right: "dayGridMonth,timeGridWeek,timeGridDay"
                      }}
                      header={{
                        left: "prev,next today",
                        center: "title",
                        right: "dayGridMonth,timeGridWeek,timeGridDay,listWeek"
                      }}
                      plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
                      ref={this.calendarComponentRef}
                      weekends={this.state.calendarWeekends}
                      events={this.state.calendarEvents}
                      dateClick={this.handleDateClick}
                      eventClick={this.handleEventClick}
                      eventResize={this.handleEventResize}
                      eventDrop={this.handleEventResize}
                    />
                  </div>
                </div>
              </Grid>

            </Paper>  
          </Grid>
        </Grid>
      </Grid>
      
    );
  }

  toggleWeekends = () => {
    this.setState({
      // update a property
      calendarWeekends: !this.state.calendarWeekends
    });
  };

  gotoPast = () => {
    let calendarApi = this.calendarComponentRef.current.getApi();
    calendarApi.gotoDate("2000-01-01"); // call a method on the Calendar object
  };

  handleDateClick = arg => {
    console.log(this.state.calendarEvents);
    this.setState({
      // add new event data
      calendarEvents: this.state.calendarEvents.concat({
        // creates a new array
        title: "New Event",
        start: arg.date,
        allDay: arg.allDay

      })
    });

  };

  handleEventClick = info => {
    info.event.remove()
    info.event.start.getTime()
    this.state.calendarEvents[1].start.getTime()
    var updatedCalEvents = this.state.calendarEvents
    for(var i in this.state.calendarEvents){
      if(this.state.calendarEvents[i].start.getTime() === info.event.start.getTime()){
        updatedCalEvents.splice(i, 1)
      }
    }

    this.setState({
      // add new event data
      calendarEvents: updatedCalEvents
    });
   


  };



  handleEventResize = info => {
    console.log(info)
    info.event.start.getTime()
    this.state.calendarEvents[1].start.getTime()
    var updatedCalEvents = []
    for(var i in this.state.calendarEvents){
      if(this.state.calendarEvents[i].start.getTime() === info.event.start.getTime()){
      }else{
        updatedCalEvents.push(this.state.calendarEvents[i])
      }
    }

    updatedCalEvents.push(info.event) ;

    this.setState({
      // add new event data
      calendarEvents: updatedCalEvents
    });
   
  };
}
