const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
  textField: {
    fontSize:50
  },
  label:{
    fontSize:50
  }
});






let AddFAQ = (props: any) => {

    const classes = useStyles(); 
    const [loading, setLoading] = useState(true)
    const [isupdate, setIsupdate] = useState(false)
    const [faqid, setFaqid] = useState(0)
    const [question, setQuestion] = useState("")
    const [answer, setAnswer] = useState("")
    const [editorcontent, setEditorcontent] = useState("")
    const [isprivate, setIsprivate] = useState(0)
    const history = useHistory();
    const [reloadGroups, setReloadGroups] = useState(0)


    useEffect(() => {
        if(reloadGroups === 0){
       
            //Getting lebensmittels list
            let method = "get";
            setIsupdate(true)
            
    
            const fetchData = async() => {
                setIsupdate(false)
                if(props.match.params.id){
                    let faqURL = "user/getFAQById/"+props.match.params.id
                    let faq = await Api(`prod`).get(faqURL,{})
                    setIsupdate(true)
                    setFaqid(faq.data.id);
                    setQuestion(faq.data.question)
                    setAnswer(faq.data.answer)

                    const sampleMarkup = faq.data.answer
                    const contentHTML = convertFromHTML(sampleMarkup)
                    const state = ContentState.createFromBlockArray(contentHTML.contentBlocks, contentHTML.entityMap)
                    const content = JSON.stringify(convertToRaw(state))
                    setEditorcontent(content)
                }
                
                setLoading(false)
            }
    
           
            fetchData();
        }
        
    
      }, [reloadGroups]);




    function createFormData(object: any, form?: FormData, namespace?: string): FormData {
        const formData = form || new FormData();
        for (let property in object) {
            if (!object.hasOwnProperty(property)) {
                continue;
            }
            const formKey = namespace ? `${namespace}|${property}` : property;
            if (object[property] instanceof Date) {
                formData.append(formKey, object[property]);
            } else if (typeof object[property] === 'object' && !(object[property] instanceof File)) {
                createFormData(object[property], formData, formKey);
            } else {
                formData.append(formKey, object[property]);
            }
        }
        return formData;
    }


    const handleSubmit = (e: any) =>{ 
        // Prevent default behavior
        e.preventDefault();

        let postData = {
            "question": question,
            "answer": answer,
            "id": faqid,
            "private": isprivate
        }

        
        const data = createFormData(postData);
        // Access FormData fields with `data.get(fieldName)`
        // For example, converting to upper case

        let url = "user/saveFAQ/"
        const saveFAQ = async() => {
            const res =  await Api(`prod`).post(url,
                {
                    "data":data,
                    "headers": {
                        'content-type': 'multipart/form-data'
                    }
                },{});
            history.push("/faq-list");
        } 
        saveFAQ();
    }

 


  return (
    
    <div>
    {loading ? (
        <Spinner isLoading={loading}  />
    ) : (
        <Grid container justify="center" spacing={4}  direction="row">
            <Grid item  xs={10} sm={10} md={10}>
                <Typography component="h1" variant="h4"> Add FAQ </Typography>
            </Grid>
            <Grid item  xs={10} sm={10} md={10}>
                <Grid container direction="column" justify="center"  className="food-diary-page">
                    <Paper variant="elevation" elevation={2} className="food-diary-background">
                        <Grid item>
                            <form method="post"  onSubmit={(e) => handleSubmit(e)}>
                                <Grid container direction="column" spacing={2} >
                                    <Grid item  xs={12} sm={12} md={12} style={{paddingTop:"20px" }}>
                                        <TextField
                                            id="question"
                                            label="Question"
                                            style={{ margin: 8 }}
                                            placeholder="Question"
                                            fullWidth
                                            name="question"
                                            margin="normal"
                                            className={classes.textField}
                                            value={question}
                                            onChange={e => setQuestion(e.target.value)}
                                        />
                                        
                                    </Grid>
                                    <Grid item xs={12} sm={12} md={12} >
                                        <MuiThemeProvider theme={defaultTheme}>
                                
                                        <MUIRichTextEditor 
                                            defaultValue={editorcontent}
                                            onChange={(e: any) => {
                                                let editorContent = stateToHTML(e.getCurrentContent());
                                                setAnswer(editorContent)
                                            }}
                                            label="Type faq answer..." 
                                        />
                                        </MuiThemeProvider>
                                    </Grid>

                                    <Grid item  xs={12} sm={12} md={12} style={{paddingTop:"20px" }}>
                                        <FormLabel component="legend">Is Private</FormLabel>
                                        <Select
                                            style={{paddingTop:"10px", minWidth: 200}}
                                            native
                                            value={isprivate}
                                            onChange={e => setIsprivate(e.target.value as number)}
                                            >
                                            <option aria-label="0" value="0" >Public</option>
                                            <option aria-label="1" value="1" >Private</option>
                                            
                                            

                                        </Select>
                                    </Grid>
                                    
                                                     
                                
                                    <Grid item xs={12} sm={12} md={12} >
                                   
                                        <Grid container style={{ paddingTop: 100 }}   justify="flex-end">
                                        
                                            <Grid item  xs={12} sm={12} md={3} style={{textAlign:"right"}}>
                                                <Button style={{marginRight:5}} fullWidth={false} onClick = {() => {history.push('/faq-list')}} variant="contained"  color="primary"  className="button-block" > Back to FAQ List </Button>
                                                <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" > Save FAQ </Button>
                                            </Grid>
                                          
                                        </Grid>
                                     

                                        
                                    </Grid>
                                </Grid>
                            </form>
                        </Grid>
                    </Paper>
                </Grid>
            </Grid>
        </Grid>

    )}
    </div>
      
    
   
     
  );
    
  
}


export default AddFAQ;