import React from 'react';
import { BrowserRouter, Switch, Route, NavLink, HashRouter } from 'react-router-dom';
import Login from './components/login/Login';
import Dashboard from './components/dashboard/Dashboard';
import Home from './components/home/Home';
import Logout from './components/logout/Logout';
import AddUser from './components/user/AddUser';
import AddReceipe from './components/receipe/AddReceipe';
import FoodList from './components/receipe/FoodList';
import AddFood from './components/receipe/AddFood';
import HandicapList from './components/receipe/HandicapList'
import AddHandicap from './components/receipe/AddHandicap'
import AddDiary from './components/diary/AddDiary';
import MyNetwork from './components/chat/MyNetwork';
import Chatroom from './components/chat/Chatroom';
import PrivateRoute from './components/common/privateroute';
import EditProfile from './components/user/EditProfile';
import Checkout from './components/stripe/Checkout';
import Schedule from './components/appointment/Schedule';
import AddSymptom from './components/diary/AddSymptom';
import AddFAQ from './components/user/AddFAQ';
import FAQList from './components/user/FAQList';
import './App.css';








function App() {
  return (
    <div className="App">
      <BrowserRouter >
      <HashRouter>
        <div>
          <div className="content">
            <Switch>
              <Route exact path="/" component={Login} />
              <Route exact path="/login" component={Login} />
              <Route path="/home" component={Home} />
              <PrivateRoute path="/dashboard" component={Dashboard} />
              <PrivateRoute path="/scheduler" component={Schedule} />
              <PrivateRoute path="/logout" component={Logout} />
              <PrivateRoute path="/add-user" component={AddUser} />
              <PrivateRoute path="/add-receipe" component={AddReceipe} />
              <PrivateRoute path="/add-diary" component={AddDiary} />
              <PrivateRoute path="/mynetwork" component={MyNetwork}/>
              <PrivateRoute path="/chatroom" component={Chatroom}/>
              <PrivateRoute path="/edit-profile-coach" component={EditProfile}/>
              <PrivateRoute path="/receipe/update/:id" component={AddReceipe}/>
              <PrivateRoute path="/diary/update/:id" component={AddDiary}/>
              <PrivateRoute path="/lebensmittel/update/:id" component={AddFood}/>
              <PrivateRoute path="/handicap/update/:id" component={AddHandicap}/>
              <PrivateRoute path="/user/edit/:id" component={AddUser}/>
              <PrivateRoute path="/user/chatroom/:id" component={Chatroom}/>
              <PrivateRoute path="/diary/symptom/update/:id" component={AddSymptom}/>
              <PrivateRoute path="/checkout" component={Checkout}/>
              <PrivateRoute path="/add-symptom" component={AddSymptom}/>
              <PrivateRoute path="/food-list" component={FoodList}/>
              <PrivateRoute path="/add-food" component={AddFood}/>
              <PrivateRoute path="/handicap-list" component={HandicapList}/>
              <PrivateRoute path="/add-handicap" component={AddHandicap}/>
              <PrivateRoute path="/add-faq" component={AddFAQ} />
              <PrivateRoute path="/faq-list" component={FAQList} />
              <PrivateRoute path="/update/faq/:id" component={AddFAQ} />
            </Switch>
          </div>
        </div>
      </HashRouter>
      </BrowserRouter>
    </div>
  );
}




export default App;