import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Grid,Paper, Typography, Button , TextField, Select, FormLabel, FormHelperText, Chip, Divider } from "@material-ui/core";
import { useHistory } from "react-router-dom";
import MUIRichTextEditor from 'mui-rte'
import { createMuiTheme, Theme, MuiThemeProvider } from '@material-ui/core/styles'
import { Api } from '../common/api';
import { EditorState, ContentState, convertFromHTML, convertToRaw } from 'draft-js'
import Spinner from "../common/Spinner";
import {stateToHTML} from 'draft-js-export-html';
import defaultFoodImage from "../../assets/food-default.png";
import Alert from '@material-ui/lab/Alert';
import Autocomplete from '@material-ui/lab/Autocomplete';


export const defaultTheme: Theme = createMuiTheme({
    palette: {
        primary: {
            main: "#000000"
        }
    }
})

Object.assign(defaultTheme, {
    overrides: {
        MUIRichTextEditor: {
            root: {
                backgroundColor: "#FCEEBF",
            },
            container: {
                display: "flex",
                flexDirection: "column-reverse"
            },
            editor: {
                backgroundColor: "#FCEEBF",
                padding: "20px",
                height: "200px",
                maxHeight: "200px",
                overflow: "auto"
            },
            toolbar: {
                borderTop: "1px solid gray",
                backgroundColor: "#ebebeb"
            },
            placeHolder: {
                backgroundColor: "#FCEEBF",
                paddingLeft: 20,
                width: "inherit",
                position: "absolute",
                top: "20px"
            },
            anchorLink: {
                color: "#333333",
                textDecoration: "underline"
            }
        }
    }
})

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
  textField: {
    fontSize:50
  },
  label:{
    fontSize:50
  }
});


interface IFood { food: string,selectedFood:any,quantity:string,food_image:string,food_image_name:string, food_image_file: any, measure:string}
let IFoodDefault = {
    "food": "",
    "selectedFood": {},
    "quantity" : "",
    "food_image": defaultFoodImage,
    "food_image_name": "",
    "food_image_file" : "",
    "measure": "gm"
}




let AddReceipe = (props: any) => {

    const classes = useStyles(); 
    const [foodList, setFoodList] = useState<IFood[]>([IFoodDefault])
    const [loading, setLoading] = useState(true)
    const [lebensmittels, setLebensmittels] = useState([])
    const [errormessagelist, setErrormessagelist] = useState<string[]>([])
    const [allergies, setAllergies] = useState([])
    const [symptoms, setSymptoms] = useState([])
    const [rsymptoms, setRsymptoms] = useState<{id:number,name:string}[]>([])
    const [isupdate, setIsupdate] = useState(false)
    const [rezepteid, setRezepteid] = useState(0)
    const [title, setTitle] = useState("")
    const [beschreibung, setBeschreibung] = useState("")
    const [editorcontent, setEditorcontent] = useState("")
    const history = useHistory();
    const [reloadGroups, setReloadGroups] = useState(0)
    const [rallergies, setRallergies] = useState<{id:number,name:string}[]>([])
    const [buchlink, setBuchlink] = useState("")
    const [buchauthor, setBuchauthor] = useState("")
    const [titletext, setTitletext] = useState("Add")

    useEffect(() => {
        if(reloadGroups === 0){
       
            //Getting lebensmittels list
            let method = "get";
            setIsupdate(true)
            
    
            const fetchData = async() => {
                
                let lebensmittelsUrl = "receipe/lebensmittels"
                let allergyUrl = "receipe/getHandicap/"
                let symptomUrl = "receipe/getSymptoms/"
                
                let respArray = []

                respArray = await Promise.all([
                  Api(`prod`).get(lebensmittelsUrl,{}),
                  Api(`prod`).get(allergyUrl,{}),
                  Api(`prod`).get(symptomUrl,{})
                ])

                let lebensmittels = respArray[0] 
                setLebensmittels(lebensmittels.data)

                let allergies = respArray[1]
                setAllergies(allergies.data)

                let symptoms = respArray[2]
                setSymptoms(symptoms.data)


                

                setIsupdate(false)
                if(props.match.params.id){
                    setTitletext("Edit")
                    let rezepteUrl = "receipe/get/id/"+props.match.params.id
                    setIsupdate(true)
                    let rezepte = await Api(`prod`).get(rezepteUrl,{})
                    setRezepteid(props.match.params.id)
                    setTitle(rezepte.data.namen)
                    const sampleMarkup = rezepte.data.beschreibung
                    const contentHTML = convertFromHTML(sampleMarkup)
                    const state = ContentState.createFromBlockArray(contentHTML.contentBlocks, contentHTML.entityMap)
                    const content = JSON.stringify(convertToRaw(state))
                    setEditorcontent(content)
                    setBeschreibung(rezepte.data.beschreibung)


                    let updatedSymptomList = [];

                    for( let symptomRowIndex in rezepte.data.symptomList){
                        updatedSymptomList.push({id: rezepte.data.symptomList[parseInt(symptomRowIndex)], name: rezepte.data.symptomNameList[parseInt(symptomRowIndex)]})
                    }

                    setRsymptoms(updatedSymptomList);


                    let updatedAllergyList = [];

                    for( let symptomRowIndex in rezepte.data.handicapList){
                        updatedAllergyList.push({id: rezepte.data.handicapList[parseInt(symptomRowIndex)], name: rezepte.data.handicapNameList[parseInt(symptomRowIndex)]})
                    }


                    setRallergies(updatedAllergyList)

                    setBuchlink(rezepte.data.buchlink)
                    setBuchauthor(rezepte.data.buchauthor)
                    let foodList: IFood[] = [];
                    


                    for(var foodIndex in rezepte.data.rezepteDictFoodList){
                        let foodObj : {rezepteLebensmitteId: number,rezepteLebensmitteName: string,bild_link: string,menge:string} = rezepte.data.rezepteDictFoodList[foodIndex]
                        let food: IFood = {
                            "food": "",
                            "selectedFood": {},
                            "quantity" : "",
                            "food_image": "",
                            "food_image_name": "",
                            "food_image_file" : "",
                            "measure":"gm"
                        };
                        food["food"] = foodObj.rezepteLebensmitteId.toString();
                        
                        try{
                            food["quantity"] = foodObj.menge.split(" ")[0]
                            food["measure"] = foodObj.menge.split(" ")[1]
                        }catch(error){

                        }

                        if(foodObj.rezepteLebensmitteId.toString().trim() !== ""){
                            food["selectedFood"] = {id: foodObj.rezepteLebensmitteId, name: foodObj.rezepteLebensmitteName}
                        }
                        
                        food["food_image"] = `${localStorage.getItem('base_backend_server_domain')}/media/${foodObj.bild_link}`
                        food["food_image_name"] = foodObj.bild_link
                        food["food_image_file"] = ""
                        await fetch(food["food_image"])
                            .then(res => res.blob()) // Gets the response and returns it as a blob
                            .then(blob => {
                                food["food_image"] = URL.createObjectURL(blob);
                                food["food_image_file"] = new File([blob], foodObj.bild_link)
                        });
                        
                        foodList.push(food)
                    }
                    setFoodList(foodList)

                }else{
                    //setFoodList([])
                }
                
                setLoading(false)
                
            }
    
           
            fetchData();
            
    
        }
        
    
      }, [reloadGroups]);


  
    const addFood = (e: any) =>{ 
        IFoodDefault = {
            "food": "",
            "selectedFood": {},
            "quantity" : "",
            "food_image": defaultFoodImage,
            "food_image_name": "",
            "food_image_file" : "",
            "measure": "gm"
        }
        e.preventDefault();
        foodList.push(
            IFoodDefault
        );

        //setFoodList(foodList)
        setReloadGroups(prev => ++prev);
    }

    function createFormData(object: any, form?: FormData, namespace?: string): FormData {
        const formData = form || new FormData();
        for (let property in object) {
            if (!object.hasOwnProperty(property) || !object[property]) {
                continue;
            }
            const formKey = namespace ? `${namespace}|${property}` : property;
            if (object[property] instanceof Date) {
                formData.append(formKey, object[property].toISOString());
            } else if (typeof object[property] === 'object' && !(object[property] instanceof File)) {
                createFormData(object[property], formData, formKey);
            } else {
                formData.append(formKey, object[property]);
            }
        }
        return formData;
    }


    const handleSubmit = (e: any) =>{ 
        // Prevent default behavior
        e.preventDefault();

        let rsymptomsArray = [];
        for(let i in rsymptoms){
            rsymptomsArray.push(rsymptoms[i].id.toString())
        }

        let rallergiesArray = [];
        for(let i in rallergies){
            rallergiesArray.push(rallergies[i].id.toString())
        }

        let postData = {
            "title": title,
            "beschreibung": beschreibung,
            "food": foodList,
            "symptoms": rsymptomsArray,
            "allergies": rallergiesArray,
            "buchlink": buchlink,
            "buchauthor": buchauthor
        }
        let errorList = []
        if(title.trim() == ""){
            errorList.push("Title field is required.")
            
        }
        if(beschreibung.replace( /(<([^>]+)>)/ig, '').trim() == ""){
            errorList.push("Beschreibung field is required.")
            
        }


        if(errorList.length > 0){
            setErrormessagelist(errorList);
            window.scrollTo(0, 0)
            setReloadGroups(prev => ++prev);
            return;
        }
        
        
        const data = createFormData(postData);
        // Access FormData fields with `data.get(fieldName)`
        // For example, converting to upper case

        if(isupdate){
            
            let url = "receipe/update/"+rezepteid
            const updateReceipe = async() => {
                try{
                    const res =  await Api(`prod`).post(url,
                    {
                        "data":data,
                        "headers": {
                            'content-type': 'multipart/form-data'
                        }
                    },{});
                    history.push("/dashboard");

                }catch(error){
                    if(error.response.data.messageList != undefined && error.response.data.messageList.length > 0){
                      setErrormessagelist(error.response.data.messageList);
                      window.scrollTo(0, 0)
                      setReloadGroups(prev => ++prev);
                    }

                }
                
            } 
            updateReceipe();
        }else{
            let method = "post";
            let url = "receipe/"

            const saveReceipe = async() => {
                try{
                    const res =  await Api(`prod`).post(url,
                    {
                        "data":data,
                        "headers": {
                            'content-type': 'multipart/form-data'
                        }
                    },{});
                    history.push("/dashboard");

                }catch(error){
                    if(error.response.data.messageList != undefined && error.response.data.messageList.length > 0){
                      setErrormessagelist(error.response.data.messageList);
                      window.scrollTo(0, 0)
                      setReloadGroups(prev => ++prev);
                    }

                }
                
            } 
            saveReceipe();
        }
    }


    const updateFood = (e: any, fieldValue: any, fieldName: string, index: any) =>{ 
        e.preventDefault();
        let  indexElement: {[fieldName:string] : any} = foodList[index];
        if(fieldName === "food_image"){
            indexElement[fieldName] =  URL.createObjectURL(e.target.files[0])
            indexElement["food_image_file"] =  e.target.files[0]
            indexElement["food_image_name"] =  e.target.files[0]['name']
        }else{
            if(fieldName == "food"){
                if(fieldValue !== null){
                    indexElement[fieldName] = fieldValue.id;
                    indexElement['selectedFood'] = fieldValue;
                }
                
            }else{
                indexElement[fieldName] = fieldValue;
            }
        }

        foodList[index] = indexElement as IFood;
        let finalFoodList : IFood[] = foodList;

        setFoodList(finalFoodList)
        setReloadGroups(prev => ++prev);

    }

  const removeItem = (e: any, i: any) =>{ 
    e.preventDefault();
    foodList.splice(i, 1);
    setFoodList(foodList)
    setReloadGroups(prev => ++prev);
  }

  return (
    
    <div>
    {loading ? (
        <Spinner isLoading={loading}  />
    ) : (
        <Grid container justify="center" spacing={4}  direction="row">
            <Grid item  xs={10} sm={10} md={10}>
                <Typography component="h1" variant="h4"> {titletext} Receipe </Typography>
                {errormessagelist.map((message: string) => (<Alert severity="error">{message}</Alert>))}
   
            </Grid>
            <Grid item  xs={10} sm={10} md={10}>
                <Grid container direction="column" justify="center"  className="food-diary-page">
                    <Paper variant="elevation" elevation={2} className="food-diary-background">
                        <Grid item>
                            <form method="post"  onSubmit={(e) => handleSubmit(e)}>
                                <Grid container direction="column" spacing={2} >
                                    <Grid item xs={12} sm={12} md={6} >
                                        <TextField
                                            id="title"
                                            label="Title"
                                            style={{ margin: 8 }}
                                            placeholder="Title"
                                            fullWidth
                                            name="title"
                                            margin="normal"
                                            className={classes.textField}
                                            value={title}
                                            onChange={e => setTitle(e.target.value)}
                                        />
                                    </Grid>
                                    <Grid item xs={12} sm={12} md={12} >
                                        <MuiThemeProvider theme={defaultTheme}>
                                
                                        <MUIRichTextEditor 
                                            defaultValue={editorcontent}
                                            onChange={(e: any) => {
                                                let editorContent = stateToHTML(e.getCurrentContent());
                                                setBeschreibung(editorContent)
                                            }}
                                            label="Type receipe detail..." 
                                        />
                                        </MuiThemeProvider>
                                    </Grid>

                                
                                    <Grid item  xs={12} sm={12} md={12}>
                                        <Typography component="h1" variant="h5"> Receipe Food List </Typography>
                                    </Grid>

                                    <Grid item xs={12} sm={12} md={12} style={{  border: 2, borderColor: "#ebebeb", borderStyle: "solid" }} >
                                        {foodList.map((food: any, index) => (
                                        <Grid container direction="row" justify="center"  className="food-diary-page" spacing={6} key={index}>
                                            <Grid item  xs={12} sm={12} md={8}>
                                                <Grid container direction="row" justify="center"  className="food-diary-page" spacing={6} key={index}>
                                                     <Grid item  xs={12} sm={12} md={12}>
                                                        <Autocomplete
                                                          id="tags-standard"
                                                          options={lebensmittels}
                                                          getOptionLabel={(option: {id:number,name:string}) => option.name}
                                                          value={food["selectedFood"]}
                                                          onChange={(e : any, value) => {
                                                              updateFood(e,value,'food',index)
                                                            }
                                                          }
                                                          renderInput={(params) => (
                                                            <TextField
                                                              {...params}
                                                              variant="standard"
                                                              placeholder="Select Lebensmittel*"
                                                            />
                                                          )}
                                                        />
                                                     </Grid>
                                                     <Grid item  xs={12} sm={12} md={12}>
                                                         <Grid container direction="row" justify="flex-start"   spacing={6} key={index}>
                                                             <Grid item  xs={12} sm={12} md={4}>
                                                                <TextField
                                                                    id="quantity"
                                                                    label="Quantity*"
                                                                    style={{ margin: 6 }}
                                                                    placeholder="Quantity"
                                                                    name="quantity"
                                                                    fullWidth
                                                                    onChange={e => updateFood(e,e.target.value,'quantity',index)}
                                                                    value = {food["quantity"]}
                                                                    margin="normal"
                                                                />
                                                             </Grid>
                                                             <Grid item  xs={12} sm={12} md={2}>
                                                                <Select
                                                                    style={{paddingTop:"20px", minWidth: 200}}
                                                                    native
                                                                    value={food["measure"]}
                                                                    onChange={e => updateFood(e,e.target.value,'measure',index)}

                                                                    >
                                                                    <option aria-label="gm" value="gm" > gm </option>
                                                                    <option aria-label="ml" value="ml" > ml </option>
                                                                    

                                                                </Select>
                                                             </Grid>
                                                         </Grid>
                                                     </Grid>

                                                     
                                                </Grid>
                                               
                                            </Grid>

                                            <Grid item xs={12} sm={12} md={4}    >
                                                <Grid container direction="column" justify="center"className="food-diary-page" >
                                                    <Grid  item  xs={12} sm={12} md={12} style={{textAlign:"center"}}>
                                                        <img alt="" src={food["food_image"]} width="200" style={{minHeight: 120}}/>
                                                    </Grid>
                                                    <Grid  item  xs={12} sm={12} md={12} style={{textAlign:"center"}}>
                                                        <Button
                                                        variant="contained"
                                                        component="label"
                                                        >
                                                            Upload Image
                                                            <input
                                                                type="file"
                                                                style={{ display: "none" }}
                                                                onChange={e => updateFood(e,e.target.value,'food_image',index)}
                                                            />
                                                        </Button>
                                                    </Grid>
                                        
                                                    
                                                </Grid>
                                            </Grid>
                                            
                                            
                                            
                                        
                                            <Grid item  xs={12}    >
                                                <Grid item   xs={12} sm={12} md={8}>
                                                    <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={(e) => removeItem(e,index)}> Remove </Button>
                                                </Grid>
                                            </Grid>
                                             <Grid item  xs={12}    >
                                                {index !== foodList.length-1 && <Divider  />}
                                            </Grid>
                                        </Grid>

                                        ))}
                                       

                                    </Grid>
                                    <Grid item  xs={12} sm={12} md={12} style={{paddingTop:"20px" }}>
                                        <Grid container   justify="flex-end">
                                            <Grid item xs={12} sm={12} md={2} style={{textAlign:"right"}} >
                                                <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={addFood}> Add more food item </Button>
                                            </Grid>
                                        </Grid>
                                    </Grid>
      
                                    <Grid item  xs={12} sm={12} md={3} >
                                      <FormLabel component="legend">Select Symptoms</FormLabel>
                                    
                                        <Autocomplete
                                          multiple
                                          id="tags-standard"
                                          options={symptoms}
                                          getOptionLabel={(option: {id:number,name:string}) => option.name}
                                          value={rsymptoms}
                                          onChange={(e : any, value) => {
                                              setRsymptoms(value)
                                            }
                                          }
                                          renderInput={(params) => (
                                            <TextField
                                              {...params}
                                              variant="standard"
                                              placeholder="Select Symptoms"
                                            />
                                          )}
                                        />
                                   
                                    </Grid>

                                    <Grid item  xs={12} sm={12} md={3} >
                                      <FormLabel component="legend">Select Allergies</FormLabel>
                                    
                                        <Autocomplete
                                          multiple
                                          id="tags-standard"
                                          options={allergies}
                                          getOptionLabel={(option: {id:number,name:string}) => option.name}
                                          value={rallergies}
                                          onChange={(e : any, value) => {
                                              setRallergies(value)
                                            }
                                          }
                                          renderInput={(params) => (
                                            <TextField
                                              {...params}
                                              variant="standard"
                                              placeholder="Select Allergies"
                                            />
                                          )}
                                        />
                                   
                                    </Grid>
                                 

                                   

                                    <Grid item  xs={12} sm={12} md={12} style={{paddingTop:"20px" }}>

                                        <TextField
                                            id="book-link"
                                            label="Book Link"
                                            style={{ margin: 6 }}
                                            placeholder="Book Link"
                                            name="booklink"
                                            fullWidth
                                            margin="normal"
                                            value={buchlink}
                                            onChange={e => setBuchlink(e.target.value)}
                                        />
                                    </Grid>

                                    <Grid item  xs={12} sm={12} md={12} style={{paddingTop:"20px" }}>
      
                                        <TextField
                                            id="book-author"
                                            label="Book Author"
                                            style={{ margin: 6 }}
                                            placeholder="Book Author"
                                            name="bookauthor"
                                            fullWidth
                                            margin="normal"
                                            value={buchauthor}
                                            onChange={e => setBuchauthor(e.target.value)}
                                        />
                                    </Grid>


                                
                                        
                                
                                    <Grid item xs={12} sm={12} md={12} >
                                   
                                        <Grid container style={{ paddingTop: 100 }}   justify="flex-end">
                                            <Grid item xs={12} sm={12} md={3} style={{textAlign:"right"}}>
                                                <Button style={{marginRight:5}} fullWidth={false} onClick = {() => {history.push('/dashboard')}} variant="contained"  color="primary"  className="button-block" > Back to Dashboard </Button>
                                                 <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" > Save Receipe </Button>
                                            </Grid>
                                  
                                          
                                        </Grid>
                                     

                                        
                                    </Grid>
                                </Grid>
                            </form>
                        </Grid>
                    </Paper>
                </Grid>
            </Grid>
        </Grid>

    )}
    </div>
      
    
   
     
  );
    
  
}


export default AddReceipe;