import 'date-fns';
import React, { useState, useEffect } from 'react';
import { Grid,Paper,Typography, Button, List, ListItem , Divider, ListItemText , TextField, TablePagination, FormLabel, Select, Chip } from "@material-ui/core";
import { useHistory, Link } from "react-router-dom";
import { Api } from '../common/api';
import MUIRichTextEditor from 'mui-rte'
import Carousel from 'react-material-ui-carousel'
import createPurify from "dompurify";
import { JSDOM } from 'jsdom'
import Spinner from "../common/Spinner";
import DateFnsUtils from '@date-io/date-fns';
import Autocomplete from '@material-ui/lab/Autocomplete';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';



const purify = createPurify();

interface Rezeptefood {
  rezepteLebensmitteId: number;
  rezepteLebensmitteName: string;
}

let FoodList = () => {

  const history = useHistory();
 
  const [buchs, setBuchs] = useState<any[]>([])
  const [page, setPage] = useState(0)
  const [totalitem, setTotalitem] = useState(0)
  const [loading, setLoading] = useState(true)
  const [buchSymptomeList, setBuchSymptomeList] = useState([])
  const [lebensmittels, setLebensmittels] = useState([])
  const [lebensmittelslist, setLebensmittelslist] = useState([])
  const [reloadGroups, setReloadGroups] = useState(0)
  const [rlebensmittels, setRlebensmittels] = useState<{id:number,name:string}[]>([])
  const [selectedDate, setSelectedDate] = React.useState(new Date(new Date(Date.now()).setHours(0,0,0,0)));
  const redirectDiaryPage = (e: any) =>{ 
    e.preventDefault();
    history.push("/add-food");
  }

  const borderDefaultProps = {
    bgcolor: 'background.paper',
    m: 1,
    border: 1,
    style: { width: '5rem', height: '5rem' },
  };

  
  useEffect(() => {
    if(reloadGroups == 0){
      const fetchData = async() => {
      setLoading(true)
      //Getting lebensmittels list
      let method = "get";
      let url = "receipe/getLebensmittelPagination/?page="+(page+1)
      let lebensmittelsUrl = "receipe/lebensmittels"


      let respArray = []

      respArray = await Promise.all([
        Api(`prod`).get(url,{}),
        Api(`prod`).get(lebensmittelsUrl,{}),
      ])

      let lebensmittels = respArray[1] 
      setLebensmittels(lebensmittels.data)

      let lebensmittelsData = respArray[0]
      setLebensmittelslist(lebensmittelsData.data.lebensmittels)
      setTotalitem(lebensmittelsData.data.count)
      
      setLoading(false)

      
    }

  
    fetchData();
  
       

    }
        
        

    
    

  }, [reloadGroups]);


  function onChangePageRow(page: any) {

    const fetchData = async() => {
      //Getting lebensmittels list
      let method = "get";
      let url = "receipe/getLebensmittelPagination/?page="+(page+1)
      let lebensmittels = await Api(`prod`).get(url,{})
      setLebensmittels(lebensmittels.data.lebensmittels)
      setPage(page)
      setReloadGroups(prev => ++prev);
      
    }

 
    fetchData();
    
  }

  const deleteDiary = (buchId: any) => {
    let url = "diary/deleteDiary/"+buchId
    const deleteReceipe = async() => {
        const res =  await Api(`prod`).post(url,
            {
                "data":{},
                "headers": {
                    'content-type': 'application/json'
                }
            },{});

        setReloadGroups(0);
       
    } 
   
    
    deleteReceipe();

  }

 const searchLebensmittel = () => {

    const searchLebensmittel = async() => {
      let searchParams = new URLSearchParams();
      let rlebensmittelsArray = [];
      for(let i in rlebensmittels){
        rlebensmittelsArray.push(rlebensmittels[i].id.toString())
      }
      searchParams.set("lebensmittels",rlebensmittelsArray.join(","));


      
      setLoading(true)
      let method = "get";
      let url = "receipe/getLebensmittelPagination/?"+searchParams.toString()
      let lebensmittels = await Api(`prod`).get(url,{})

      if(lebensmittels.data.lebensmittels == undefined){
        setLebensmittelslist([])
        setTotalitem(0)

      }else{
        setLebensmittelslist(lebensmittels.data.lebensmittels)
        setTotalitem(lebensmittels.data.count)
      }
      
      setReloadGroups(prev => ++prev);
      setLoading(false)
    } 
   
    
    searchLebensmittel();
    
  }

  const addDiaryBtnPermission = () => {
    if(localStorage.getItem('userRole') != null){
      switch(localStorage.getItem('userRole')) {
        case "admin":  return   <Button style={{margin:"10px"}} fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={redirectDiaryPage}> Add Food </Button>;;
        case "coach":  return   <div></div>;
        case "client": return   <div></div>
        default:      return <div></div>
      }
    }
  }



const handleDateChange = ((date: any) => {
  date = new Date(date.setHours(0,0,0,0));
  setSelectedDate(date);
});
  

  return (
    (loading)?(<Spinner isLoading={loading}  />):(
      <Grid container justify="center" spacing={4}  direction="row">
   
          <Grid item  xs={12} sm={12} md={10}>
            {addDiaryBtnPermission()}
          </Grid>
          <Grid item   xs={12} sm={12} md={10} className="food-diary-page">
            <Paper variant="elevation" elevation={2} className="food-diary-background" style={{minHeight:0}}>
              <Grid container direction="row" spacing={4} >
                <Grid item  xs={12} sm={12} md={3} >
                  
                
                    <Autocomplete
                      style={{paddingTop:"30px"}}
                      multiple
                      id="tags-standard"
                      options={lebensmittels}
                      getOptionLabel={(option: {id:number,name:string}) => option.name}
                      value={rlebensmittels}
                      onChange={(e : any, value) => {
                          setRlebensmittels(value)
                        }
                      }
                      renderInput={(params) => (
                        <TextField
                          {...params}
                          variant="standard"
                          placeholder="Select Lebensmittel"
                        />
                      )}
                    />
               
                </Grid>
             
                <Grid item  xs={12} sm={12} md={3} style={{textAlign: "right"}}>
                  <Button fullWidth={false}   variant="contained" color="primary" type="submit" className="button-block" onClick={searchLebensmittel} style={{marginTop:16,marginRight:5}}> Search </Button>
                   <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={() => {setLoading(true);setReloadGroups(0);setLoading(false);}} style={{marginTop:16}}> Reset </Button>
                </Grid>
              </Grid>
            </Paper>
          </Grid>
          
          <Grid item   xs={12} sm={12} md={10} className="food-diary-page" >
            <Paper variant="elevation" elevation={2} className="food-diary-background" >
              <List >
              
              {lebensmittelslist.map((lebensmittel: any, index) => (
                <div>
                
                <ListItem alignItems="flex-start" >
                  <Grid container justify="center" spacing={4}  direction="row" >
                    <Grid item  xs={12} sm={12} md={6}>
                      {lebensmittel.name}                     
                    </Grid>
                    <Grid item  xs={12} sm={12} md={6}>
                      <Button  className="button-block" to={`/lebensmittel/update/${lebensmittel.id}`} component={Link}>
                        Edit
                      </Button>                     
                    </Grid>
                    </Grid>
                    
                </ListItem>
                <Divider variant="inset" component="li" style={{marginLeft:0}} />
                </div>
                
              ))}
              
              </List>
              <TablePagination
                component="nav"
                page={page}
                rowsPerPageOptions={[]}
                rowsPerPage={5}
                count={totalitem}
                onChangePage={(e, page) => onChangePageRow(page)}
              />
            </Paper>
          </Grid>
      </Grid>

    )
      

     
  );
}


export default FoodList;