import React, { useState, useEffect } from 'react';
import { Grid,Paper,Typography, Button, List, ListItem, FormLabel,Select,TextareaAutosize, Avatar,  Divider, ListItemText , TextField, TablePagination, Chip } from "@material-ui/core";
import { useHistory, Link } from "react-router-dom";
import { Api } from '../common/api';
import MUIRichTextEditor from 'mui-rte'
import Carousel from 'react-material-ui-carousel'
import createPurify from "dompurify";
import { JSDOM } from 'jsdom'
import Spinner from "../common/Spinner";
import Autocomplete from '@material-ui/lab/Autocomplete';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { makeStyles } from '@material-ui/core/styles';


const purify = createPurify();

interface Rezeptefood {
  rezepteLebensmitteId: number;
  rezepteLebensmitteName: string;
}


const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  small: {
    width: theme.spacing(3),
    height: theme.spacing(3),
  },
  large: {
    width: theme.spacing(15),
    height: theme.spacing(15),
  },
}));


let ReceipeList = () => {

  const history = useHistory();

  const classes = useStyles();
 
  const [rezeptes, setRezeptes] = useState<any[]>([])
  const [page, setPage] = useState(0)
  const [totalitem, setTotalitem] = useState(0)
  const [reloadGroups, setReloadGroups] = useState(0)
  const [forcereloadGroups, setForcereloadGroups] = useState(0)
  const [lebensmittels, setLebensmittels] = useState([])
  const [rlebensmittels, setRlebensmittels] = useState<{id:number,name:string}[]>([])
  const [rsymptoms, setRsymptoms] = useState<{id:number,name:string}[]>([])
  const [rallergies, setRallergies] = useState<string[]>([])
  const [allergies, setAllergies] = useState([])
  const [symptoms, setSymptoms] = useState([])
  const [loading, setLoading] = useState(true)
  const [redescription, setRdescription] = useState("")
  const [open, setOpen] = React.useState(false);
  const [selectedreceipe, setSelectedrecipe] = useState(null)



  //Search Parameters
  const [searchtitle, setSearchtitle] = useState("")
  const [searchsymptoms, setSearchsymptoms] = useState("")



  const redirectAddReceipe = (e: any) =>{ 
    e.preventDefault();
    history.push("/add-receipe");
  }

  const borderDefaultProps = {
    bgcolor: 'background.paper',
    m: 1,
    border: 1,
    style: { width: '5rem', height: '5rem' },
  };

  const handleClickOpen = (id:any) => {
    setSelectedrecipe(id);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };


  
  useEffect(() => {
      //setSearchtitle((state : string) => (state));
      if(reloadGroups == 0){
         const fetchData = async() => {
            setLoading(true)
            let lebensmittelsUrl = "receipe/lebensmittels"
            let allergyUrl = "receipe/getHandicap/"
            let symptomUrl = "receipe/getSymptoms/"
            let allRezepteURL = "receipe/get/all?page="+(page+1)
            
            let respArray = []

            respArray = await Promise.all([
              Api(`prod`).get(lebensmittelsUrl,{}),
              Api(`prod`).get(allergyUrl,{}),
              Api(`prod`).get(symptomUrl,{}),
              Api(`prod`).get(allRezepteURL,{})
            ])

            let lebensmittels = respArray[0] 
            setLebensmittels(lebensmittels.data)

            let allergies = respArray[1]
            setAllergies(allergies.data)

            let symptoms = respArray[2]
            setSymptoms(symptoms.data)

            let rezeptes = respArray[3]
            setRezeptes(rezeptes.data.rezeptes)
            setTotalitem(rezeptes.data.count)
            setLoading(false)

        }

      
        fetchData();
      }
      
  }, [reloadGroups, forcereloadGroups]);


  function onChangePageRow(page: any) {

    const fetchData = async() => {
      //Getting lebensmittels list
      let method = "get";
      let url = "receipe/get/all?page="+(page+1)
      let rezeptes = await Api(`prod`).get(url,{})
      setRezeptes(rezeptes.data.rezeptes)
      setPage(page)
      setReloadGroups(prev => ++prev);
      
    }

 
    fetchData();
    
  }

  const deleteReceipe = (receipeId: any) => {
    let url = "receipe/delete/"+receipeId
    const deleteReceipe = async() => {
        const res =  await Api(`prod`).post(url,
            {
                "data":{},
                "headers": {
                    'content-type': 'application/json'
                }
            },{});

        
        handleClose()
        setLoading(true);setReloadGroups(0);setForcereloadGroups(prev => ++prev);setLoading(false);

       
    } 
   
   
    deleteReceipe();

  }


  const searchReceipe = () => {

    const searchReceipe = async() => {
      let searchParams = new URLSearchParams();
      searchParams.set("title",searchtitle);
      searchParams.set("description",redescription);

      let symptomsArray = [];
      for(let i in rsymptoms){
        symptomsArray.push(rsymptoms[i].id.toString())
      }
      searchParams.set("lebensmittels",symptomsArray.join(","));

      searchParams.set("symptoms",symptomsArray.join(","));

      let rlebensmittelsArray = [];
      for(let i in rlebensmittels){
        rlebensmittelsArray.push(rlebensmittels[i].id.toString())
      }
      searchParams.set("lebensmittels",rlebensmittelsArray.join(","));


      
      setLoading(true)
      let method = "get";
      let url = "receipe/searchRezepte/?"+searchParams.toString()
      let rezeptes = await Api(`prod`).get(url,{})

      if(rezeptes.data.rezeptes == undefined){
        setRezeptes([])
        setTotalitem(0)
      }else{
        setRezeptes(rezeptes.data.rezeptes)
        setTotalitem(rezeptes.data.count)
      }
      
      setReloadGroups(prev => ++prev);
      setLoading(false)
    } 
   
    
    searchReceipe();
    
  }

  const addReceipeBtnPermission = () => {
    if(localStorage.getItem('userRole') != null){
      switch(localStorage.getItem('userRole')) {
        case "admin":  return <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={redirectAddReceipe}> Add Receipe </Button>;
        case "coach":  return   <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={redirectAddReceipe}> Add Receipe </Button>;
        case "client": return    <div></div>;
        default:      return <div></div>
      }
    }
}


let getEditDeleteRecipeButton = (rezepte:any) => {
    if(localStorage.getItem('userRole') != null){
      switch(localStorage.getItem('userRole')) {
        case "admin":  return (
                            <div>
                                <Button  className="button-block" to={`/receipe/update/${rezepte.id}`} component={Link}>
                                  Edit
                                </Button>
                                <Button style={{backgroundColor: "#d11a2a"}} fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={(e) => handleClickOpen(rezepte.id)}> Delete </Button>
                            </div>
                        );
        case "coach":  return   (
                            <div>
                                <Button  className="button-block" to={`/receipe/update/${rezepte.id}`} component={Link}>
                                  Edit
                                </Button>
                                <Button style={{backgroundColor: "#d11a2a"}} fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={(e) => handleClickOpen(rezepte.id)}> Delete </Button>
                            </div>
                        );
        case "client": return    <div></div>;
        default:      return <div></div>
      }
    }
}
  

  return (
    (loading)?(<Spinner isLoading={loading}  />):(
      <Grid container justify="center" spacing={4}  direction="row"> 
          <Grid item  xs={12}>
            <Dialog
              open={open}
              onClose={handleClose}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title">{"Are you sure you want to delete this receipe?"}</DialogTitle>
              
              <DialogActions>
                <Button onClick={handleClose} color="primary">
                  Cancel
                </Button>
                <Button onClick={() => {deleteReceipe(selectedreceipe)}} color="primary" autoFocus>
                  Continue
                </Button>
              </DialogActions>
            </Dialog>
          </Grid>
          <Grid item  xs={12}>
            {addReceipeBtnPermission()}
          </Grid>
          <Grid item   xs={12} className="food-diary-page">
            <Paper variant="elevation" elevation={2} className="food-diary-background" style={{minHeight:0}}>
              <Grid container direction="row" spacing={4} >
                  <Grid item xs={12} sm={12} md={3}  >
                      <TextField
                          id="title"
                          label="Title"
                          style={{ margin: 8 }}
                          placeholder="Title"
                          fullWidth
                          name="title"
                          value={searchtitle}
                          margin="normal"
                          onChange={(e) => {
                            setSearchtitle(e.target.value)
                          }}
                      />
                  </Grid>
                  <Grid item xs={12} sm={12} md={2} >
   
                    <TextareaAutosize  
                        onChange={(e) => {
                          setRdescription(e.target.value)
                        }}
                       style={{marginTop:6}} 
                       aria-label="Search Description" 
                       rowsMin={3} 
                       value={redescription}
                       placeholder="Search By Description" 
                    />
                  </Grid>
                  <Grid item  xs={12} sm={12} md={3} >
                    
                  
                    <Autocomplete
                      multiple
                      id="tags-standard"
                      options={lebensmittels}
                      getOptionLabel={(option: {id:number,name:string}) => option.name}
                      value={rlebensmittels}
                      onChange={(e : any, value) => {
                          setRlebensmittels(value)
                        }
                      }
                      renderInput={(params) => (
                        <TextField
                          {...params}
                          variant="standard"
                          placeholder="Search By Lebensmittel"
                        />
                      )}
                    />
                 
                </Grid>
                <Grid item  xs={12} sm={12} md={3} >
                   
                  
                    <Autocomplete
                      multiple
                      id="tags-standard"
                      options={symptoms}
                      getOptionLabel={(option: {id:number,name:string}) => option.name}
                      value={rsymptoms}
                      onChange={(e : any, value) => {
                          setRsymptoms(value)
                        }
                      }
                      renderInput={(params) => (
                        <TextField
                          {...params}
                          variant="standard"
                          placeholder="Search By Symptoms"
                        />
                      )}
                    />
                 
                </Grid>
               
                
                <Grid item  xs={12} sm={12} md={3} style={{textAlign: "right"}}>
                  <Button fullWidth={false}   variant="contained" color="primary" type="submit" className="button-block" onClick={searchReceipe} style={{marginTop:16,marginRight:5}}> Search </Button>
                  <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={() => {setLoading(true);setReloadGroups(0);setLoading(false);}} style={{marginTop:16}}> Reset </Button>
                </Grid>

              </Grid>
            </Paper>
          </Grid>

          <Grid item   xs={12} className="food-diary-page" style={{color:"#000"}}>
            
              <List >
              
              {rezeptes.map((rezepte: any, index) => (
                <div>
                <Paper variant="elevation" elevation={1} className="food-diary-background" style={{marginBottom:"40px"}}>
                
                <ListItem alignItems="flex-start" >
                  <Grid container spacing={4}  direction="row" >
                    <Grid item  xs={12} sm={12} md={12}>
                      <Typography variant="h4" gutterBottom> {rezepte.namen} </Typography>
                    </Grid>
                    <Grid item  xs={12} sm={12} md={6}>
                      <Grid container justify="center" spacing={4}  direction="row" >
                        <Grid item  xs={12} sm={12} md={12}>
                          <div dangerouslySetInnerHTML={{ __html: purify.sanitize( rezepte.beschreibung) }} /> 
                        </Grid>
                        <Grid item  xs={12} sm={12} md={12}>
                          <b>For Symptoms: </b>{rezepte.symptomNameList.join(", ")} 
                        </Grid>
                        <Grid item  xs={12} sm={12} md={12}>
                          <b>Avoid this receipe if you have: </b>{rezepte.handicapNameList.join(", ")} 
                        </Grid>
                      </Grid>
                      </Grid>
                    {(rezepte.rezepteDictFoodList.length > 0)?( <Grid item  xs={12} sm={12} md={6}>
                        <List>
                        {rezepte.rezepteDictFoodList.map((rezeptefood: any,index: any) => (
                          <ListItem>
                          
                  
                              <Grid container justify="center" spacing={4}  direction="row">
                                <Grid item  xs={12} sm={12} md={3}  style={{textAlign: "center"}}>
                                  <Avatar className={classes.large} variant="rounded" src={`${localStorage.getItem('base_backend_server_domain')}/media/${rezeptefood['bild_link']}`} />
                                </Grid>
                                <Grid item  xs={12} sm={12} md={9}  >
                                  <Grid container justify="center" spacing={4}  direction="row">
                                    <Grid item  xs={12} sm={12} md={12}  >
                                      <Typography component="h1" variant="h5"> {rezeptefood["rezepteLebensmitteName"]} </Typography>
                                    </Grid>
                                    
                                    <Grid item  xs={12} sm={12} md={12} >
                                      <b>menge:</b> {rezeptefood["menge"]} 
                                    </Grid>
                                  </Grid>
                                </Grid>
                                 <Grid item  xs={12}    >
                                    {index !== rezepte.rezepteDictFoodList.length-1 && <Divider  />}
                                </Grid>
                               
                                
                              </Grid>
                  
                       
                        
                          </ListItem>
                          
                        ))}
                        </List>
                      </Grid>


                      ):("")}
                   

                   
                    
                    
                      <Grid item  xs={12} sm={12} md={12} style={{textAlign: "right"}}>
                        {getEditDeleteRecipeButton(rezepte)}
                      </Grid>

            
                   
                    </Grid>
                    
                </ListItem>
                </Paper>
         
                </div>
                
              ))}
              
              </List>
              <TablePagination
                component="nav"
                page={page}
                rowsPerPageOptions={[]}
                rowsPerPage={5}
                count={totalitem}
                onChangePage={(e, page) => onChangePageRow(page)}
              />
         
          </Grid>



         
        </Grid>
      )
      

                    

     
  );
}


export default ReceipeList;