import 'date-fns';
import React, { useState, useEffect } from 'react';
import { Grid,Paper,Typography, Button, List, ListItem , Divider, ListItemText , Avatar,  TextField, TablePagination, FormLabel, Select, Chip } from "@material-ui/core";
import { useHistory, Link } from "react-router-dom";
import { Api } from '../common/api';
import MUIRichTextEditor from 'mui-rte'
import Carousel from 'react-material-ui-carousel'
import createPurify from "dompurify";
import { JSDOM } from 'jsdom'
import Spinner from "../common/Spinner";
import DateFnsUtils from '@date-io/date-fns';
import Autocomplete from '@material-ui/lab/Autocomplete';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { makeStyles } from '@material-ui/core/styles';



const purify = createPurify();

interface Rezeptefood {
  rezepteLebensmitteId: number;
  rezepteLebensmitteName: string;
}

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  small: {
    width: theme.spacing(3),
    height: theme.spacing(3),
  },
  large: {
    width: theme.spacing(15),
    height: theme.spacing(15),
  },
}));

let DiaryList = () => {

   const classes = useStyles();

  const history = useHistory();
 
  const [buchs, setBuchs] = useState<any[]>([])
  const [page, setPage] = useState(0)
  const [totalitem, setTotalitem] = useState(0)
  const [loading, setLoading] = useState(true)
  const [forcereloadGroups, setForcereloadGroups] = useState(0)
  const [buchSymptomeList, setBuchSymptomeList] = useState([])
  const [lebensmittels, setLebensmittels] = useState([])
  const [reloadGroups, setReloadGroups] = useState(0)
  const [rlebensmittels, setRlebensmittels] = useState<{id:number,name:string}[]>([])
  const [selectedDate, setSelectedDate] = React.useState(new Date(new Date(Date.now()).setHours(0,0,0,0)));
  const [open, setOpen] = React.useState(false);
  const [selecteddiaryid, setSelecteddiaryid] = useState(null)
  const redirectDiaryPage = (e: any) =>{ 
    e.preventDefault();
    history.push("/add-diary");
  }

  const borderDefaultProps = {
    bgcolor: 'background.paper',
    m: 1,
    border: 1,
    style: { width: '5rem', height: '5rem' },
  };

  
  useEffect(() => {
    if(reloadGroups == 0){
      const fetchData = async() => {
      setLoading(true)
      //Getting lebensmittels list
      let method = "get";
      let url = "diary/getAllBuch/?page="+(page+1)
      let lebensmittelsUrl = "receipe/lebensmittels"


      let respArray = []

      respArray = await Promise.all([
        Api(`prod`).get(lebensmittelsUrl,{}),
        Api(`prod`).get(url,{}),
      ])

      let lebensmittels = respArray[0] 
      setLebensmittels(lebensmittels.data)

      let buchs = respArray[1]
      if(buchs.data.buchs == undefined && page != 0){
        let method = "get";
        let url = "diary/getAllBuch/?page=0"
        let buchs = await Api(`prod`).get(url,{})
        setBuchs(buchs.data.buchs)
        setPage(page -1)
      }else{
        setBuchs(buchs.data.buchs)
        setTotalitem(buchs.data.count)
        setLoading(false)
      }

      
    }

  
    fetchData();
  
       

    }
        
        

    
    

  }, [reloadGroups, forcereloadGroups]);


  function onChangePageRow(page: any) {

    const fetchData = async() => {
      //Getting lebensmittels list
      let method = "get";
      let url = "diary/getAllBuch/?page="+(page+1)
      let buchs = await Api(`prod`).get(url,{})
      setBuchs(buchs.data.buchs)
      setPage(page)
      setReloadGroups(prev => ++prev);
      
    }

 
    fetchData();
    
  }

  const deleteDiary = (buchId: any) => {
    let url = "diary/deleteDiary/"+buchId
    const deleteReceipe = async() => {
        const res =  await Api(`prod`).post(url,
            {
                "data":{},
                "headers": {
                    'content-type': 'application/json'
                }
            },{});
        handleClose()
        setLoading(true);setReloadGroups(0);setForcereloadGroups(prev => ++prev);setLoading(false);

       
    } 
   
    
    deleteReceipe();

  }

 const searchDiary = () => {

    const searchReceipe = async() => {
      let searchParams = new URLSearchParams();
      let rlebensmittelsArray = [];
      for(let i in rlebensmittels){
        rlebensmittelsArray.push(rlebensmittels[i].id.toString())
      }
      searchParams.set("lebensmittels",rlebensmittelsArray.join(","));
      searchParams.set("selectedDate",new Date(selectedDate.setHours(0,0,0,0)).toISOString());
      searchParams.set("endTime",new Date(selectedDate.setHours(23,59,59,999)).toISOString());

      
      setLoading(true)
      let method = "get";
      let url = "diary/searchDiary/?"+searchParams.toString()
      let buchs = await Api(`prod`).get(url,{})

      if(buchs.data.buchs == undefined){
        setBuchs([])
        setTotalitem(0)
      }else{
        setBuchs(buchs.data.buchs)
        setTotalitem(buchs.data.count)
      }
      
      setReloadGroups(prev => ++prev);
      setLoading(false)
    } 
   
    
    searchReceipe();
    
  }

   const handleClickOpen = (id:any) => {
    setSelecteddiaryid(id);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const addDiaryBtnPermission = () => {
    if(localStorage.getItem('userRole') != null){
      switch(localStorage.getItem('userRole')) {
        case "admin":  return  <div></div>;
        case "coach":  return   <div></div>;
        case "client": return    <Button style={{margin:"10px"}} fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={redirectDiaryPage}> Add Food </Button>;
        default:      return <div></div>
      }
    }
  }

  const addSymptomBtnPermission = () => {
    if(localStorage.getItem('userRole') != null){
      switch(localStorage.getItem('userRole')) {
        case "admin":  return  <div></div>;
        case "coach":  return   <div></div>;
        case "client": return    <Button style={{margin:"10px"}} fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={()=>{history.push('/add-symptom')}}> Add Symptom </Button>;
        default:      return <div></div>
      }
    }
  }

const handleDateChange = ((date: any) => {
  date = new Date(date.setHours(0,0,0,0));
  setSelectedDate(date);
});
  

  return (
    (loading)?(<Spinner isLoading={loading}  />):(
      <Grid container justify="center" spacing={4}  direction="row">
          <Grid item  xs={12}>
            <Dialog
              open={open}
              onClose={handleClose}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title">{"Are you sure you want to delete this diary item?"}</DialogTitle>
              
              <DialogActions>
                <Button onClick={handleClose} color="primary">
                  Cancel
                </Button>
                <Button onClick={() => {deleteDiary(selecteddiaryid)}} color="primary" autoFocus>
                  Continue
                </Button>
              </DialogActions>
            </Dialog>
          </Grid>
   
          <Grid item  xs={12}>
            {addDiaryBtnPermission()}
            {addSymptomBtnPermission()}
          </Grid>
          <Grid item   xs={12} sm={12} md={12} className="food-diary-page">
            <Paper variant="elevation" elevation={2} className="food-diary-background" style={{minHeight:0}}>
              <Grid container direction="row" spacing={4} >
                <Grid item  xs={12} sm={12} md={3} >
                  
                
                    <Autocomplete
                      style={{paddingTop:"30px"}}
                      multiple
                      id="tags-standard"
                      options={lebensmittels}
                      getOptionLabel={(option: {id:number,name:string}) => option.name}
                      value={rlebensmittels}
                      onChange={(e : any, value) => {
                          setRlebensmittels(value)
                        }
                      }
                      renderInput={(params) => (
                        <TextField
                          {...params}
                          variant="standard"
                          placeholder="Select Lebensmittel"
                        />
                      )}
                    />
               
                </Grid>
                <Grid item   xs={12} sm={12} md={3} className="food-diary-page">
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                      <KeyboardDatePicker
                        margin="normal"
                        id="diary-date"
                        label="Select Date"
                        format="MM/dd/yyyy"
                        value={selectedDate}
                        onChange={handleDateChange}
                        KeyboardButtonProps={{
                          'aria-label': 'change date',
                        }}
                      />
                  </MuiPickersUtilsProvider>

                </Grid>
                <Grid item  xs={12} sm={12} md={3} style={{textAlign: "right"}}>
                  <Button fullWidth={false}   variant="contained" color="primary" type="submit" className="button-block" onClick={searchDiary} style={{marginTop:16,marginRight:5}}> Search </Button>
                   <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={() => {setLoading(true);setReloadGroups(0);setLoading(false);}} style={{marginTop:16}}> Reset </Button>
                </Grid>
              </Grid>
            </Paper>
          </Grid>
          <Grid item   xs={12} className="food-diary-page" style={{color:"#000"}}>
            
              <List >
              
              {buchs.map((buch: any, index) => (
                <div>
                <Paper variant="elevation" elevation={1} className="food-diary-background" style={{marginBottom:"40px"}}>
                
                <ListItem alignItems="flex-start" >
                  <Grid container spacing={4}  direction="row" >
                    {(buch.buchDictFoodList.length > 0)?( <Grid item  xs={12} sm={12} md={8}>
                        <List>
                        {buch.buchDictFoodList.map((buchfood: any,index: any) => (
                          <ListItem>
                          
                  
                              <Grid container justify="center" spacing={4}  direction="row">
                                <Grid item  xs={12} sm={12} md={3}  style={{textAlign: "center"}}>
                                  <Avatar className={classes.large} variant="rounded" src={`${localStorage.getItem('base_backend_server_domain')}/media/${buchfood['bild_link']}`} />
                                </Grid>
                                <Grid item  xs={12} sm={12} md={9}  >
                                  <Grid container justify="center" spacing={4}  direction="row">
                                    <Grid item  xs={12} sm={12} md={12}  >
                                      <Typography component="h1" variant="h5"> {buchfood["buchLebensmitteName"]} </Typography>
                                    </Grid>
                                    
                                    <Grid item  xs={12} sm={12} md={12} >
                                      <b>menge:</b> {buchfood["menge"]} 
                                    </Grid>
                                  </Grid>
                                </Grid>
                                 <Grid item  xs={12}    >
                                    {index !== buch.buchDictFoodList.length-1 && <Divider  />}
                                </Grid>
                               
                                
                              </Grid>
                  
                       
                        
                          </ListItem>
                          
                        ))}
                        </List>
                      </Grid>


                      ):("")}
                   

                    <Grid item  xs={12} sm={12} md={4}>
                      <Grid container  spacing={4}  direction="row" >
                        {(buch.type != "symptome")?(
                          <Grid item  xs={12} sm={12} md={12}>
                            <b>Meal Taken</b>: {buch.meal}
                          </Grid>
                        ):("")}

                        {(buch.type != "symptome")?(
                          <Grid item  xs={12} sm={12} md={12}>
                            <b>Emotion</b>: {buch.emotion}
                          </Grid>
                        ):("")}

                        {(buch.type == "symptome")?(
                          <Grid item  xs={12} sm={12} md={12}>
                            <b>Symptoms</b>: {buch.buchSymptomNameList.join(", ")}
                          </Grid>
                        ):("")}


                        
                        
                        
                        <Grid item  xs={12} sm={12} md={12}>
                          <b>Date</b>: {(buch.uhrzeit!= undefined)?(new Date(new Date(buch.uhrzeit)).toDateString()):("")}
                        </Grid>
                        <Grid item  xs={12} sm={12} md={12}>
                          <b>Time</b>: {(buch.uhrzeit!= undefined)?(new Date(new Date(buch.uhrzeit)).toTimeString()):("")}
                        </Grid>

                         <Grid item  xs={12} sm={12} md={12}>
                          {(buch.nutrition.calories != 0)?(<Chip style={{margin:"5px"}} color="primary" size="small" label={"Calories: "+buch.nutrition.calories} />):("")}
                          {(buch.nutrition.kilojoule != 0)?(<Chip style={{margin:"5px"}} color="primary" size="small" label={"Kilojoule: "+buch.nutrition.kilojoule} />):("")}
                          {(buch.nutrition.breadunit != 0)?(<Chip style={{margin:"5px"}} color="primary" size="small" label={"Breadunit: "+buch.nutrition.breadunit} />):("")}
                          {(buch.nutrition.carbohydrates != 0)?(<Chip style={{margin:"5px"}} color="primary" size="small" label={"Carbohydrates: "+buch.nutrition.carbohydrates} />):("")}
                          {(buch.nutrition.bold != 0)?(<Chip style={{margin:"5px"}} color="primary" size="small" label={"Bold: "+buch.nutrition.bold} />):("")}
                          {(buch.nutrition.protein != 0)?(<Chip style={{margin:"5px"}} color="primary" size="small" label={"Protein: "+buch.nutrition.protein} />):("")}
                          {(buch.nutrition.csalary != 0)?(<Chip style={{margin:"5px"}} color="primary" size="small" label={"Csalary: "+buch.nutrition.csalary} />):("")}
                        </Grid>

                      </Grid>                      
                    </Grid>
                    
                    
                    
                      <Grid item  xs={12} sm={12} md={12} style={{textAlign: "right"}}>
                        {(buch.type == "symptome")?(
                          <Button  className="button-block" to={`/diary/symptom/update/${buch.id}`} component={Link}>
                            Edit
                          </Button>
                        ):(
                          <Button  className="button-block" to={`/diary/update/${buch.id}`} component={Link}>
                            Edit
                          </Button>
                        )}
                        
                        
                        <Button style={{backgroundColor: "#d11a2a"}} fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={(e) => handleClickOpen(buch.id)}> Delete </Button>
                      </Grid>
                      

            
                   
                    </Grid>
                    
                </ListItem>
                </Paper>
         
                </div>
                
              ))}
              
              </List>
              <TablePagination
                component="nav"
                page={page}
                rowsPerPageOptions={[]}
                rowsPerPage={5}
                count={totalitem}
                onChangePage={(e, page) => onChangePageRow(page)}
              />
         
          </Grid>
      </Grid>

    )
      

     
  );
}


export default DiaryList;