import React, { Component } from 'react';
import { dummyUsers } from "./Users";
import { withRouter } from 'react-router-dom';
import { Grid,Paper,Typography, Button, TextField, TextareaAutosize  } from "@material-ui/core";
import Rating from '@material-ui/lab/Rating';
import { Api } from '../common/api';
import defaultProfilePic from "../../assets/default-profile.jpg";
import StripeCheckout from 'react-stripe-checkout';
import Alert from '@material-ui/lab/Alert';
import { connect } from "react-redux";
import { compose } from 'redux'
import Avatar from '@material-ui/core/Avatar';
import { CountryDropdown, RegionDropdown, CountryRegionData } from 'react-country-region-selector';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

class MyNetwork extends Component {
    constructor() {
        super();

        let title = "";
        let userUrl = "";

        if(localStorage.getItem('userRole') == "client"){
            title = "My Coaches";
            userUrl =  "user/?role=coach";
        }else if(localStorage.getItem('userRole') == "coach"){
            title = "My Clients";
            userUrl =  "user/?role=client";
        }else{
            title = "User List";
            userUrl =  "user/";
        }

        this.state = {
            title: title,
            userUrl: userUrl,
            checkoutAlert: false,
            users: [],
            searchemail: "",
            country:"",
            region:"",
            description:""
        };


       
    }



    componentDidMount() {


        let fetchuser = async() => {
            let users = await Api(`prod`).get(this.state.userUrl,{})
            this.setState({
                users : users.data.result
            });

        }
        
        fetchuser();
        this.setState({
            checkoutAlert: false
        });
        
    }



    redirectAddUser = (e) =>{ 
        this.props.history.push(`/add-user`)
    }


    addUserBtnPermission = () => {
        if(localStorage.getItem('userRole') != null){
          switch(localStorage.getItem('userRole')) {
            case "admin":  return <Grid item   xs={12} sm={12} md={12}><Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={this.redirectAddUser}> Add User </Button></Grid>;
            case "coach":  return   "";
            case "client": return    "";
            default:      return ""
          }
        }
        
    }
    getCookie = (name) => {
        var cookieValue = null;
        if (document.cookie && document.cookie !== '') {
            var cookies = document.cookie.split(';');
            for (var i = 0; i < cookies.length; i++) {
                var cookie = cookies[i].trim();
                if (cookie.substring(0, name.length + 1) === (name + '=')) {
                    cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                    break;
                }
            }
        }
        return cookieValue;
    }

    
    onToken = (token,user_id,price) => {

        const sendChargeReq = async(token,user_id,price) => {
            const _csrfToken = null;

            let url = "user/csrf/"
            let tokenData = await Api(`prod`).get(url,{})

            let csrfToken = tokenData.data.csrfToken;
           
        
            await Api(`prod`).post('user/charge/',
            {
                "data":JSON.stringify({
                    token: token.id,
                    amount: price * 100,
                    description: "Coach Booking Payment",
                    person_id: user_id,
                  }),
                "headers": {
                    'content-type': 'application/json',
                    'X-CSRFToken': csrfToken
                }
            },{});
            this.setState({
                checkoutAlert: true
            });

        }
        sendChargeReq(token,user_id,price)
        

    }

    bookBtnPermission = (user_id,price) => {
        if(localStorage.getItem('userRole') != null){
          switch(localStorage.getItem('userRole')) {
            case "admin":  return <div></div>;
            case "coach":  return   <div></div>;
            case "client": return    <div>{(price)?(<span><StripeCheckout
                token={(token) => {this.onToken(token,user_id,price)}}
                stripeKey="pk_test_51HdGfLHdkoE92aVGZVZH9iX7tD4BdmZfRxq3kKBcSYqglUCtOa1Jfq6z1aXS2nV1IdUzY3JOleMArUHNnASdHXIg00kXCi6ztL"
              /></span>):(<span></span>)}</div>;
            default:      return <div></div>
          }
        }
    }

    getPriceText = (price) => {
        if(localStorage.getItem('userRole') != null){
          switch(localStorage.getItem('userRole')) {
            case "admin":  return <div></div>;
            case "coach":  return   <div></div>;
            case "client": return    <div>{(price)?(<span>${price}</span>):(<span>$0</span>)}</div>;;
            default:      return <div></div>
          }
        }
    }

    getUserEditButton = (userid) => {
        if(localStorage.getItem('userRole') != null){
          switch(localStorage.getItem('userRole')) {
            case "admin":  return <button style={{marginLeft: "10px"}} onClick={()=>{this.props.history.push('/user/edit/'+userid)}}>Edit </button>;
            case "coach":  return   <div></div>;
            case "client": return    <div></div>;;
            default:      return <div></div>
          }
        }
    }

    searchUser = () => {

        const searchUser = async() => {
            let searchParams = new URLSearchParams();
            searchParams.set("email",this.state.searchemail);     
            searchParams.set("country",this.state.country);     
            searchParams.set("region",this.state.region);  
            searchParams.set("description",this.state.description);
            searchParams.set("role",localStorage.getItem('userRole'));   

              
          
            let method = "get";
            let url = "user/searchUser/?"+searchParams.toString()
            let users = await Api(`prod`).get(url,{})

            if(users.data.users != undefined){
                this.setState({
                    users : users.data.users
                });
            }


        } 


        searchUser();
    
    }



   
    

    render() {
     

        return (
            <Grid container justify="center" spacing={4}  direction="row">
                {(this.state.title.trim() != "")?(
                <Grid item  xs={12} sm={12} md={12} >
                    <Typography component="h1" variant="h5"> {this.state.title} </Typography>
                </Grid>):("")}
                
                
                {this.addUserBtnPermission()}
                
                {(this.state.checkoutAlert)?
                    <Grid item   xs={12} sm={12} md={12}><Alert severity="success">Checkout Successful</Alert></Grid>
                :
                    ""
                }
                <Grid item   xs={12} sm={12} md={12} className="food-diary-page">
                    <Paper variant="elevation" elevation={2} className="food-diary-background" style={{minHeight:0}}>
                      <Grid container direction="row" spacing={4} >
                        <Grid item  xs={12} sm={12} md={3} >
                          <TextField
                              id="email"
                              label="Email"
                              style={{ margin: 8 }}
                              placeholder="Email"
                              fullWidth
                              name="email"
                              value={this.state.searchemail}
                              margin="normal"
                              onChange={(e) => {
                                   this.setState({
                                       searchemail:e.target.value
                                   })
                              }}
                          />

                        </Grid>
                    
                        <Grid item  xs={12} sm={12} md={3}  spacing={2} >
                          <div className="country-region-select">
                            <CountryDropdown
                              value={this.state.country}
                              onChange={(val) => this.setState({country: val}) } />
                            <RegionDropdown
                              country={this.state.country}
                              value={this.state.region}
                              onChange={(val) => this.setState({region: val}) } />
                          </div>
                        </Grid>

                        <Grid item xs={12} sm={12} md={3} >
   
                          <TextareaAutosize  
                              onChange={(e) => {
                                this.setState({
                                  description:e.target.value
                                })
                              }}
                             style={{marginTop:6}} 
                             aria-label="Search Description" 
                             rowsMin={3} 
                             value={this.state.description}
                             placeholder="Search By Description" 
                          />
                        </Grid>
                        
                          

                        
                        
                        <Grid item  xs={12} sm={12} md={3} style={{textAlign: "right"}}>
                          <Button fullWidth={false}   variant="contained" color="primary" type="submit" className="button-block" onClick={this.searchUser} style={{marginTop:16,marginRight:5}}> Search </Button>
                           <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={() => {this.componentDidMount();}}  style={{marginTop:16}}> Reset </Button>
                        </Grid>
                      </Grid>
                    </Paper>
                  </Grid>
                
                <Grid item    xs={12} sm={12} md={12} className="food-diary-page">
                    <Paper variant="elevation" elevation={2} className="food-diary-background" >
                        <div className="users">
                    

                            <div className="users-container"> 
                                <ul>
                                    { this.state.users.map(user => 
                                        <li key={user.id} className="user">
                                            <Grid container justify="center" spacing={4}  direction="row">
                                                
                                                <Grid item  xs={12} sm={12} md={12}  spacing={2} >
                                                  <Accordion>
                                                    <AccordionSummary
                                                      expandIcon={<ExpandMoreIcon />}
                                                      aria-controls="panel2a-content"
                                                      id="panel2a-header"
                                                    >
                                                      <Grid container justify="center" spacing={4}  direction="row">
                                                        <Grid item  xs={12} sm={12} md={2} >
                                                          <picture className="user-picture">
                                                              {(user.profilepic != "" && user.profilepic != null)?(
                                                                  <Avatar src={`${localStorage.getItem('base_backend_server_domain')}/media/${user.profilepic}`} alt={`${user.email}`} />
                                                              ):(
                                                                  <Avatar src={defaultProfilePic} alt={`${user.email}`} />
                                                              )
                                                              }
                                                      
                                                          </picture>
                                                        </Grid>
                                                        <Grid item  xs={12} sm={12} md={3} >
                                                            <div className="user-info">
                                                                <h4>{user.email}</h4>
                                                            </div>
                                                        </Grid>
                                                        <Grid item  xs={12} sm={12} md={1} >
                                                            {this.getPriceText(user.price)}
                                                        </Grid>
                                                        <Grid item  xs={12} sm={12} md={2} >
                                                            {this.bookBtnPermission(user.id,user.price)}
                                                        </Grid>
                                                        <Grid item  xs={12} sm={12} md={2} >
                                                            <Rating
                                                                style = {{alignItems: "center", justifyContent: "center"}}
                                                                name="hover-feedback"
                                                                value={2}
                                                                precision={0.5}
                                                                onChange={(event, newValue) => {
                                                                    
                                                                }}
                                                                onChangeActive={(event, newHover) => {
                                                                    
                                                                }}
                                                            />
                                                        </Grid>
                                                        <Grid item  xs={12} sm={12} md={2} className="user-action">
                                                            <button onClick={()=>{this.props.history.push('/user/chatroom/'+user.id)}}>Message </button>
                                                            {this.getUserEditButton(user.id)}
                                                        </Grid>
                                                       
                                                      </Grid>
                                                    </AccordionSummary>
                                                    <AccordionDetails>
                                                      <Typography>
                                                        {user.description}
                                                       
                                                      </Typography>
                                                    </AccordionDetails>
                                                  </Accordion>
                                                </Grid>
                                            </Grid>
                                            
                                           
                                          
                                        </li>
                                    )}
                                </ul>

                                
                            </div>
                        </div>
                    </Paper>
                </Grid>
            </Grid>
        )
    }
}

const mapStateToProps = state => ({
    loginSuccessResponse: state.auth.loginSuccessResponse
});


export default connect(mapStateToProps)(withRouter(MyNetwork));
   
