import React, { useState, useEffect } from 'react';
import { Grid,Paper,Typography, Button, List, ListItem, FormLabel,Select,TextareaAutosize,  Divider, ListItemText , TextField, TablePagination, Chip } from "@material-ui/core";
import { useHistory, Link } from "react-router-dom";
import { Api } from '../common/api';
import MUIRichTextEditor from 'mui-rte'
import Carousel from 'react-material-ui-carousel'
import createPurify from "dompurify";
import { JSDOM } from 'jsdom'
import Spinner from "../common/Spinner";
import Autocomplete from '@material-ui/lab/Autocomplete';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';


const purify = createPurify();

interface Rezeptefood {
  rezepteLebensmitteId: number;
  rezepteLebensmitteName: string;
}

let FAQList = () => {

  const history = useHistory();
 
  const [faqs, setFaqs] = useState<any[]>([])
  const [page, setPage] = useState(0)
  const [totalitem, setTotalitem] = useState(0)
  const [reloadGroups, setReloadGroups] = useState(0)
  const [forcereloadGroups, setForcereloadGroups] = useState(0)
  const [lebensmittels, setLebensmittels] = useState([])
  const [rlebensmittels, setRlebensmittels] = useState<{id:number,name:string}[]>([])
  const [rsymptoms, setRsymptoms] = useState<{id:number,name:string}[]>([])
  const [rallergies, setRallergies] = useState<string[]>([])
  const [allergies, setAllergies] = useState([])
  const [symptoms, setSymptoms] = useState([])
  const [loading, setLoading] = useState(true)
  const [redescription, setRdescription] = useState("")
  const [open, setOpen] = React.useState(false);
  const [selectedfaq, setSelectedfaq] = useState(null)



  //Search Parameters
  const [searchtitle, setSearchtitle] = useState("")
  const [searchsymptoms, setSearchsymptoms] = useState("")



  const redirectAddReceipe = (e: any) =>{ 
    e.preventDefault();
    history.push("/add-faq");
  }

  const borderDefaultProps = {
    bgcolor: 'background.paper',
    m: 1,
    border: 1,
    style: { width: '5rem', height: '5rem' },
  };

  const handleClickOpen = (id:any) => {
    setSelectedfaq(id);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };


  
  useEffect(() => {
      //setSearchtitle((state : string) => (state));
      if(reloadGroups == 0){
         const fetchData = async() => {
            setLoading(true)
            let faqURL = "user/faq/all?page="+(page+1)
            
            let respArray = []

            respArray = await Promise.all([
              Api(`prod`).get(faqURL,{})
            ])

           
            let faqs = respArray[0]
            setFaqs(faqs.data.faqs)
            setTotalitem(faqs.data.count)
            setLoading(false)

        }

      
        fetchData();
      }
      
  }, [reloadGroups, forcereloadGroups]);


  function onChangePageRow(page: any) {

    const fetchData = async() => {
      //Getting lebensmittels list
      let method = "get";
      let url = "user/faq/all?page="+(page+1)
      let faqs = await Api(`prod`).get(url,{})
      setFaqs(faqs.data.faqs)
      setPage(page)
      setReloadGroups(prev => ++prev);
      
    }

 
    fetchData();
    
  }

  const deleteFAQ = (faqid: any) => {
    let url = "user/faq/delete/"+faqid
    const deleteFAQ = async() => {
        const res =  await Api(`prod`).post(url,
            {
                "data":{},
                "headers": {
                    'content-type': 'application/json'
                }
            },{});

        
        handleClose()
        setLoading(true);setReloadGroups(0);setForcereloadGroups(prev => ++prev);setLoading(false);

       
    } 
   
   
    deleteFAQ();

  }




  const addFAQBtnPermission = () => {
    if(localStorage.getItem('userRole') != null){
      switch(localStorage.getItem('userRole')) {
        case "admin":  return <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={redirectAddReceipe}> Add FAQ </Button>;
        case "coach":  return   <div></div>;
        case "client": return    <div></div>;
        default:      return <div></div>
      }
    }
}


let getEditDeleteFAQButton = (faq:any) => {
    if(localStorage.getItem('userRole') != null){
      switch(localStorage.getItem('userRole')) {
        case "admin":  return (
                            <div>
                                <Button  className="button-block" to={`/update/faq/${faq.id}`} component={Link}>
                                  Edit
                                </Button>
                                <Button style={{backgroundColor: "#d11a2a"}} fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={(e) => handleClickOpen(faq.id)}> Delete </Button>
                            </div>
                        );
        case "coach":  return   (
                            <div>
                                <Button  className="button-block" to={`/update/faq/${faq.id}`} component={Link}>
                                  Edit
                                </Button>
                                <Button style={{backgroundColor: "#d11a2a"}} fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={(e) => handleClickOpen(faq.id)}> Delete </Button>
                            </div>
                        );
        case "client": return    <div></div>;
        default:      return <div></div>
      }
    }
}
  

  return (
    (loading)?(<Spinner isLoading={loading}  />):(
      <Grid container justify="center" spacing={4}  direction="row"> 
          <Grid item  xs={12}>
            <Dialog
              open={open}
              onClose={handleClose}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title">{"Are you sure you want to delete this FAQ?"}</DialogTitle>
              
              <DialogActions>
                <Button onClick={handleClose} color="primary">
                  Cancel
                </Button>
                <Button onClick={() => {deleteFAQ(selectedfaq)}} color="primary" autoFocus>
                  Continue
                </Button>
              </DialogActions>
            </Dialog>
          </Grid>
          <Grid item  xs={12} sm={12} md={10}>
            {addFAQBtnPermission()}
          </Grid>
         
          <Grid item   xs={12} sm={12} md={10} className="food-diary-page">
            <Paper variant="elevation" elevation={2} className="food-diary-background" >
              <List >
              
              {faqs.map((faq: any, index) => (
                <div>
                
                <ListItem alignItems="flex-start" >
                  <Grid container justify="center" spacing={4}  direction="row" >
                    <Grid item  xs={12} sm={12} md={12}>
                        <h2>{faq.question}</h2>
                    </Grid>
                    <Grid item  xs={12} sm={12} md={12}>
                      <div dangerouslySetInnerHTML={{ __html: purify.sanitize( faq.answer) }} /> 
                    </Grid>
                  
                   
                      <Grid item  xs={12} sm={12} md={12} style={{textAlign: "right"}}>
                        {getEditDeleteFAQButton(faq)}
                      </Grid>
                      

            
                   
                    </Grid>
                    
                </ListItem>
                <Divider variant="inset" component="li" style={{marginLeft:0}} />
                </div>
                
              ))}
              
              </List>
              <TablePagination
                component="nav"
                page={page}
                rowsPerPageOptions={[]}
                rowsPerPage={5}
                count={totalitem}
                onChangePage={(e, page) => onChangePageRow(page)}
              />
            </Paper>
          </Grid>
        </Grid>
      )
      

                    

     
  );
}


export default FAQList;